import { createStore, applyMiddleware, combineReducers  } from 'redux';
import thunk from 'redux-thunk';

import loadingReducer from './reducers/loadingReducer';
import loginReducer from './reducers/loginReducer'
import carouselTitleReducer from './reducers/carouselTitleReducer'
import majorMenuReducer from './reducers/majorMenuReducer'
import isShowMajorMenuReducer from './reducers/isShowMajorMenuReducer'
import level2MenuReducer from './reducers/level2MenuReducer'

const rootReducer = combineReducers({
	loadingReducer,
	loginReducer,
	carouselTitleReducer,
	majorMenuReducer,
	isShowMajorMenuReducer,
	level2MenuReducer
});

const store = createStore(
	rootReducer,
	applyMiddleware(thunk)
);

export default store