
const defaultState = {
    majorMenuUrl: 'all',
    currentMajorMenu: '会计专业'
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case "0":
            return {
                majorMenuUrl: 'all',
                currentMajorMenu: '会计专业'
            }
        case "1":
            return {
                majorMenuUrl: 'kuaiji',
                currentMajorMenu: '会计专业'
            }
        case "2":
            return {
                majorMenuUrl: 'jisuanji',
                currentMajorMenu: '计算机网络技术专业'
            }
        case "3":
            return {
                majorMenuUrl: 'qichedianzi',
                currentMajorMenu: '汽车电子技术专业'
            }
        case "4":
            return {
                majorMenuUrl: 'qicheyingxiao',
                currentMajorMenu: '汽车营销与服务专业'
            }
        case "5":
            return {
                majorMenuUrl: 'qicheyunyong',
                currentMajorMenu: '汽车运用维修专业'
            }
        case "6":
            return {
                majorMenuUrl: 'shukong',
                currentMajorMenu: '数控技术专业'
            }
        default:
            return state
    }
}