import {
	LEVEL2_MENU
} from './../actionTypes';


const defaultState = {
	currentLevel2Menu: ''
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case LEVEL2_MENU:
            return {
                currentLevel2Menu: JSON.stringify(action.obj)
            }
        default:
            return state
    }
}