
const defaultState = {
	carouseltitle: '学校层面'
}

export default (state = defaultState, action) => {
    switch(action.type) {
        case -1: 
            return {
                carouseltitle: ''
            }
        case 0:
            return {
                carouseltitle: '学校层面'
            }
        case 1:
            return {
                carouseltitle: '专业层面'
            }
        case 2:
            return {
                carouseltitle: '教师层面'
            }
        case 3:
            return {
                carouseltitle: '课程层面'
            }
        default:
            return state
    }
}